<template>
	<view>
		<view class="score"><text class="text">得分</text><text class="number">{{score}}</text></view>
		<view class="error"><text class="text">错误</text><text class="number">{{error}}</text></view>
		<PengpaiFadeInOut :duration="1000" :wait="3000" :top="50" :left="45" :radius="30" :loop="true"
			:info="item" v-for="(item, index) in messageList" :key="index"></PengpaiFadeInOut>
	</view>
</template>

<script>
	import {
		EventBus
	} from "../../pages/pianoGame/event-bus.js"
	import PengpaiFadeInOut from "../Pengpai-FadeInOut/Pengpai-FadeInOut.vue"

	export default {
		data() {
			return {
				score: 0, // 玩家得分数
				// second: 0,	// 游戏计时 秒数
				error: 0, // 玩家失分数
				status: 0, // 正确状态 1表示正确 2表示错误
				messageList: [], // 消息队列
			}
		},
		components: {
			PengpaiFadeInOut,
		},
		mounted() {
			EventBus.$on("addScore", (value) => {
				this.status = 0
				console.log("加分", value)
				this.score += 1
				this.messageList = this.messageList.concat({ img:"data:image/png;base64,", title: "正确" })
				if (this.score == 999) {
					// 提示游戏结束还有称赞之类的
					// 游戏重置
					this.resetGame()
				}
				setTimeout(()=>{this.messageList.pop()}, 1000)
				EventBus.$emit("changeNote", 1)
			})
			EventBus.$on("reduceScore", (value) => {
				this.status = 0
				console.log("错误", value)
				this.error += 1
				this.messageList = this.messageList.concat({ img:"data:image/png;base64,", title: "错误" })
				if (this.score == 999) {
					// 提示游戏结束还有鼓励之类的
					// 游戏重置
					this.resetGame()
				}
				setTimeout(()=>{this.messageList.pop()}, 1000)
			})
		},
		methods: {
			resetGame() {
				this.score = 0
				this.error = 0
			}
		}
	}
</script>

<style>
	.error {
		margin-left: 20rpx;
		color: red;
	}

	.text {
		letter-spacing: .07em;
		font-size: 1.3rem;
		font-weight: bold;
	}

	.number {
		margin-left: 60rpx;
		font-size: 1.1rem;
		font-weight: bold;
	}

	.score {
		margin-left: 20rpx;
		color: orange;
	}

	.animate-center {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 128rpx;
		height: 128rpx;
		margin-left: -64rpx;
		margin-top: -64rpx;
	}
</style>
